<template>
  <div id="meta-params">
    <table class="table">
      <tr>
        <td>コンテンツID：{{id}}</td>
        <td>名称：{{name}}</td>
        <td>種別：{{kind}}</td>
      </tr>
      <tr>
        <td>教科：{{subject}}</td>
        <td>対象学年：{{grade}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "MetaParams",
  computed: {
    id: function(){
      return this.$store.state.meta.metaId
    },
    name: function(){
      return this.$store.state.meta.metaName
    },
    kind: function(){
      return this.$store.state.meta.metaKind
    },
    subject: function(){
      return this.$store.state.meta.metaSubject
    },
    grade: function(){
      return this.$store.state.meta.metaGrade
    }
  }
}
</script>

<style scoped>
#meta-params{
  width: 80%;
  font-size: 20px;
}
</style>