<template>
  <div style="width: 50%; font-size: 20px;">
    <div id="pdf">
      <button id="prev" class="r-btn btn btn-secondary" @click="onPrevPage"
      style="font-size: 25px; margin-right: 5px;">Previous</button>
      <button id="next" class="r-btn btn btn-secondary" @click="onNextPage"
      style="font-size: 25px; margin-right: 5px;">Next</button>
      <button id="next" class="r-btn btn btn-secondary" @click="onInput"
      style="font-size: 25px; margin-right: 5px;">ファイル読み込み</button>
      <input type="file" @change="inputFile" ref="input" style="display: none;">
      &nbsp; &nbsp;
      <span>Page:
        <input id="page_num" type="text" v-model="pageNum"
        @change="setPageNum" @keyup.enter="pageMove"
        style="font-size: 20px;"> / 
        <span>{{pageCount}}</span>
      </span>
      <div>
        <canvas id="the-canvas" ref="canvas"></canvas>
        <div class="textLayer" ref="textLayer" v-html="innerHtml" @click="catchText"></div>
      </div>
    </div>
  </div>
</template>

<script>
var pdfjsLib= window['pdfjs-dist/build/pdf']
pdfjsLib.GlobalWorkerOptions.workerSrc= '//mozilla.github.io/pdf.js/build/pdf.worker.js'

export default {
  name: 'PDFArea',
  data: function(){
    return {
      innerHtml: null,
      txt: "",
      canvas: null,
      ctx: null,
      pdfDoc: localStorage.getItem("pdf"),
      pageNum: null,
      pageRendering: false,
      pageNumPending: null,
      scale: 1,
      pageCount: null
    }
  },
  computed: {
    pageNums: function(){
      return this.$store.state.register.pageNum
    }
  },
  methods: {
    onInput: function(){
      this.$refs.input.click()
    },
    setPageNum: function(){
      this.$store.dispatch("register/setPageNum",{num: this.pageNum})
    },
    inputFile: function(e){
      localStorage.clear()
      var self = this
      var file = e.target.files[0]
      var reader = new FileReader();
      reader.onload = () => {
        this.$store.dispatch("loading/startLoad")
        var load = pdfjsLib.getDocument(reader.result)
        load.promise.then(pdf => {
          localStorage.setItem("pdf",pdf)
          self.pdfDoc = pdf
          self.pageNum = 1
          self.pageCount = self.pdfDoc.numPages
          self.renderPage(self.pageNum)
        }).then(() => {
          this.$store.dispatch("loading/endLoad")
        })
      }
      reader.readAsDataURL(file)
    },
    renderPage: function(num){
      this.$store.dispatch("register/setPageNum",{num: 1})
      this.$refs.textLayer.textContent = null
      this.pageRendering = true
      this.pdfDoc.getPage(num).then(page => {
        var viewport = page.getViewport({scale: this.scale})
        this.canvas.height = viewport.height
        this.canvas.width = viewport.width
        var renderContext= {
          canvasContext: this.ctx,
          viewport: viewport
        };
        var renderTask= page.render(renderContext)
        renderTask.promise.then(() => {
          this.pageRendering= false
          if (this.pageNumPending !== null) {
            this.renderPage(this.pageNumPending)
            this.pageNumPending= null
          }
        }).then(() => {
          return page.getTextContent()
        }).then(textContent => {
          var textLayer = this.$refs.textLayer
          textLayer.style.left = this.canvas.offsetLeft + "px"
          textLayer.style.top = this.canvas.offsetTop + "px"
          textLayer.style.height = this.canvas.offsetHeight + "px"
          textLayer.style.width = this.canvas.offsetWidth + "px"
          pdfjsLib.renderTextLayer({
            textContent: textContent,
            container: textLayer,
            viewport: viewport,
            textDivs: []
          })
        })
      })
    },
    pdfCheck: function(){
      if(localStorage.getItem("pdf") === null){
        return -1
      }
    },
    onPrevPage: function(){
      if(this.pdfCheck() === -1){
        return
      }
      if(this.pageNum <= 1){
        return
      }
      this.pageNum--
      this.queueRenderPage(this.pageNum)
    },
    onNextPage: function(){
      if(this.pdfCheck() === -1){
        return
      }
      if(this.pageNum >= this.pdfDoc.numPages){
        return
      }
      this.pageNum++
      this.queueRenderPage(this.pageNum)
    },
    queueRenderPage: function(num){
      if(this.pageRendering){
        this.pageNumPending = num
      }else{
        this.renderPage(num)
      }
    },
    pageMove: function(){
      var num = Number(this.pageNum)
      if(this.pdfCheck() === -1)return
      if(!num)return
      num = num < 1 ? 1:num
      num = num > this.pdfDoc.numPages ? this.pdfDoc.numPages:num
      this.$store.dispatch("register/setPageNum",{num: num})
      this.renderPage(num)
    },
    catchText: function(){
      this.txt = window.getSelection().toString()
      this.$store.dispatch("register/setText",{txt: this.txt})
    }
  },
  mounted(){
    //canvas関連初期設定
    this.canvas = this.$refs.canvas
    this.ctx = this.canvas.getContext("2d")
    localStorage.clear()
  },
  watch: {
    pageNums: function(){
      this.pageNum = this.$store.state.register.pageNum
    }
  }
}
</script>

<style>
#prev,#next{
  font-size: 20px;
  font: auto;
}
#page_num{
  width: 30px;
}
.textLayer {
  top: auto;
}
</style>