<template>
  <div style="width: 50%; font-size: 25px;">
    <div style="display: flex; height: 50px;">
      <textarea id="input" v-model="inputText"
      placeholder="ここに入力してください" @change="txtInput"></textarea>
      <button id="s-btn" class="btn btn-secondary btn-lg" @click="imi">
        検索
      </button>
    </div><br>
    <div style="height: 400px; overflow: scroll;">
      <div v-for="r in result" v-bind:key="r.no"
      style="display: flex; border-bottom: solid;">
        <div class="text-center" style="width: 10%; margin: auto;">
          <button @click="selectCode" class="btn btn-secondary btn-lg"
          style="font-size: 30px;">
            選択
          </button>
        </div>
        <div style="width: 90%;">
          <div>ヒット率：{{r.score}}</div>
          <div>学習指導要領テキスト：<br>{{r.text}}</div>
          <div>学習指導要領コード：<span>{{r.code}}</span></div>
        </div>
      </div>
    </div><br>
    <div style="height: 200px;">
      <div style="display: flex;">
        <span>学習指導要領コード：</span>
        <div>
          <div v-for="(c,index) in codeList" :key="c">
            <input type="text" class="fonts" v-model="codeList[index]"
            @change="codeChange">
            <button type="button" class="close" data-dismiss="modal"
            @click="deleteCode(index)">&times;</button>
            <br>
          </div>
        </div><br>
      </div>
      <label>
        コンテンツアクセス情報：
        <input type="text" class="fonts" v-model="access" @change="accessChange">
      </label><br>
      <label>
        コメント：
        <input type="text" class="fonts" v-model="comment" @change="commentChange">
      </label><br>
      <div style="display: flex;">
        <div style="width: 45%;">
          <button id="c-btn" class="btn btn-secondary btn-lg btn-block"
          @click="openDialog">
            確認
          </button>
        </div>
        <span style="width: 10%;"></span>
        <div style="width: 45%;">
          <button id="r-btn" class="btn btn-secondary btn-lg btn-block"
          @click="register">
            登録
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchArea',
  data: function(){
    return {
      inputText: "",
      access: "",
      comment: "",
      result: [],
      codeList: []
    }
  },
  computed: {
    txt: function(){
      return this.$store.state.register.txt
    },
    results: function(){
      return this.$store.state.register.result
    },
    codes: function(){
      return this.$store.state.register.code
    },
    accesses: function(){
      return this.$store.state.register.access
    },
    comments: function(){
      return this.$store.state.register.comment
    }
  },
  methods: {
    openDialog: function(){
      this.$store.dispatch("register/dialogOpen")
    },
    txtInput: function(e){
      this.$store.dispatch("register/setText",{txt: e.target.value})
    },
    codeChange: function(){
      this.$store.dispatch("register/setCode",{code: this.codeList})
    },
    accessChange: function(){
      this.$store.dispatch("register/setAccess",{access: this.access})
    },
    commentChange: function(){
      this.$store.dispatch("register/setComment",{comment: this.comment})
    },
    register: function(){
      var no = this.$store.state.register.watcher
      var id = this.$store.state.meta.metaId
      var code = this.$store.state.register.code
      var access = this.$store.state.register.access
      var comment = this.$store.state.register.comment
      var txt = this.$store.state.register.txt

      if(!id){
        alert("コンテンツIDを入力してください")
        return
      }
      if(code.length === 0){
        alert("学習指導要領コードを入力してください")
        return
      }
      if(!access){
        alert("コンテンツアクセス情報を入力してください")
        return
      }

      for(var i=0;i<code.length;i++){
        var info = {
          no: no,
          content_id: id,
          gakushu_shidoyoryo_code: code[i],
          access_info: access,
          comment: comment,
          search_string: txt
        }
        this.$store.dispatch("register/pushCsvOutput",{info: info})
      }
      this.openDialog()
      this.$store.dispatch("dialog/onHighLight")
    },
    imi: function(){
      var data = []
      this.axios.post("/api/imi",{txt: this.txt})
        .then(res => {
          var r = res.data
          for(let i=0;i<r.length;i++){
            data.push({
              no: r[i].value["@id"],
              score: r[i].score,
              code: r[i].value["表記"],
              text: r[i].value["説明"]
            })
          }
          this.$store.dispatch("register/setResult",{result: data})
        })
        .catch(e => {
          console.log(e)
        })
    },
    selectCode: function(e){
      var getCode = e.target.parentElement.parentElement.children[1].children[2].children[0].textContent
      this.codeList.push(getCode)
      this.$store.dispatch("register/setCode",{code: this.codeList})
    },
    deleteCode: function(index){
      this.codeList.splice(index,1)
      this.$store.dispatch("register/setCode",{code: this.codeList})
    }
  },
  watch: {
    txt: function(){
      this.inputText = this.$store.state.register.txt
    },
    results: function(){
      this.result = this.$store.state.register.result
    },
    codes: function(){
      this.codeList = this.$store.state.register.code
    },
    accesses: function(){
      this.access = this.$store.state.register.access
    },
    comments: function(){
      this.comment = this.$store.state.register.comment
    }
  }
}
</script>

<style scoped>
#input{
  width: 80%;
}
#s-btn,#c-btn,#r-btn,.fonts,.close{
  font-size: 25px;
}
#s-btn{
  margin: auto;
}
</style>