<template>
  <LoadMetaDlg/>
  <CheckInfoDlg/>
  <MoreInfo/>
  <MetaArea/>
  <div style="display: flex;">
    <PDFArea/>
    <SearchArea/>
  </div>
  <div class="wrap" v-show="show">
    <vue-loaders name="ball-beat" color="red" scale="1"></vue-loaders>
  </div>
</template>

<script>
import MetaArea from "./components/MetaArea.vue"
import PDFArea from "./components/PDFArea.vue"
import SearchArea from "./components/SearchArea.vue"
import LoadMetaDlg from "./components/LoadMetaDlg.vue"
import CheckInfoDlg from "./components/CheckInfoDlg.vue"
import MoreInfo from "./components/MoreInfo.vue"

export default {
  name: 'App',
  components: {
    MetaArea,
    PDFArea,
    SearchArea,
    LoadMetaDlg,
    CheckInfoDlg,
    MoreInfo
  },
  data: function(){
    return {
      show: this.$store.state.loading.loading
    }
  },
  computed: {
    shows: function(){
      return this.$store.state.loading.loading
    }
  },
  methods: {
  },
  mounted (){
    window.parent.onbeforeunload = function(e){
      return e
    }
  },
  watch: {
    shows: function(){
      this.show = this.$store.state.loading.loading
    }
  }
}
</script>

