<template>
  <div id="overlay" v-show="showContent" @click="closeDialog"></div>
  <div id="content" style="font-size: 20px;" v-show="showContent">
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h2>デジタルコンテンツ検索支援サイト登録情報</h2>
          <button type="button" class="close" data-dismiss="modal"
          @click="closeDialog">&times;</button>
        </div>
        <div class="modal-body">
          <button class="r-btn btn btn-secondary" @click="allRemove"
          style="margin-right: 5px;">
            すべて削除する
          </button>
          <button class="r-btn btn btn-secondary" @click="outputCsv"
          style="margin-right: 5px;">
            CSVファイルを出力する
          </button>
          <input type="file" style="display: none" ref="input"
          @change="inputCSV">
          <button class="r-btn btn btn-secondary" @click="clickInputCSV"
          style="margin-right: 5px;">
            CSVファイルを読み込む
          </button><br>
          <div id="result-table">
            <table class="table" ref="table">
              <tr>
                <th>コンテンツID</th>
                <th>学習指導要領コード</th>
                <th>アクセス情報</th>
                <th>コメント</th>
                <th>検索文字列</th>
                <th></th>
              </tr>
              <tr v-for="c in csvOutput" v-bind:key="c.no"
              :class="{'table-success': last(c.no)}">
                <td style="display: none">{{c.no}}</td>
                <td>{{c.content_id}}</td>
                <td>{{c.gakushu_shidoyoryo_code}}</td>
                <td>{{c.access_info}}</td>
                <td>{{c.comment}}</td>
                <td>{{c.search_string}}</td>
                <td>
                  <button @click="changeBtn($event)"
                  class="btn btn-secondary btn-lg"
                  style="margin-right: 2px;">
                    変更
                  </button>
                  <button @click="deleteBtn($event)"
                  class="btn btn-secondary btn-lg">
                    削除
                  </button>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CheckInfoDlg",
  data: function(){
    return {
      showContent: this.$store.state.register.showContent,
      csvOutput: this.$store.state.csvOutput
    }
  },
  computed: {
    show: function(){
      return this.$store.state.register.showContent
    },
    watcher: function(){
      return this.$store.state.register.watcher
    }
  },
  methods: {
    closeDialog: function(){
      this.$store.dispatch("register/dialogClose")
      this.$store.dispatch("dialog/offHighLight")
    },
    changeBtn: function(e){
      this.$store.dispatch("register/dialogClose")
      this.$store.dispatch("dialog/offHighLight")
      var my = e.target
      var child = my.parentElement.parentElement.children
      this.$store.dispatch("register/setCode",{code: [child[2].textContent]})
      this.$store.dispatch("register/setAccess",{access: child[3].textContent})
      this.$store.dispatch("register/setComment",{comment: child[4].textContent})
      this.$store.dispatch("register/setText",{txt: child[5].textContent})
    },
    deleteBtn: function(e){
      this.$store.dispatch("dialog/offHighLight")
      var my = e.target
      var child = my.parentElement.parentElement.children
      var no = child[0].textContent
      this.csvOutput.some((v,i) => {
        if(v.no == no){
          this.csvOutput.splice(i,1)
        }
      })
      this.$store.dispatch("register/deleteCsvOutput",{csv: this.csvOutput})
    },
    allRemove: function(){
      this.$store.dispatch("dialog/offHighLight")
      this.csvOutput = []
      this.$store.dispatch("register/deleteCsvOutput",{csv: this.csvOutput})
    },
    outputCsv: function(){
      this.$store.dispatch("dialog/offHighLight")
      var csv = this.csvOutput
      var text = "content_id,gakushu_shidoyoryo_code,access_info,comment,search_string\n"
      text += "コンテンツID,学習指導要領コード,アクセス情報,コメント,検索文字列\n"
      for(let i=0;i<Object.keys(csv).length;i++){
        text += csv[i].content_id + "," + csv[i].gakushu_shidoyoryo_code + "," +
                csv[i].access_info + "," + csv[i].comment + "," +
                csv[i].search_string + "\n"
      }
      var blob = new Blob(
        ["\ufeff" + text],
        { "type": "text/plain" }
      )
      let link = document.createElement("a")
      link.href = window.URL.createObjectURL(blob)
      link.download = "output.csv"
      link.click()
    },
    last(no){
      if(!this.$store.state.dialog.highLight){
        return false
      }
      if(no === this.csvOutput[this.csvOutput.length-1].no){
        return true
      }
      return false
    },
    clickInputCSV(){
      this.$refs.input.click()
    },
    inputCSV(e){
      this.$store.dispatch("dialog/offHighLight")
      var self = this
      this.$store.state.register.csvOutput.length = 0
      var reader = new FileReader()
      var file = e.target.files[0]
      let linesArray = []
      reader.readAsText(file)
      reader.onload = () => {
        let lines = reader.result.split("\n")
        var info = {}
        lines.forEach(line => {
          linesArray.push(line.split(","))
        })
        var key = linesArray[0]
        for(var i=2;i<linesArray.length-1;i++){
          info = {
            no: this.$store.state.register.watcher,
            [key[0]]: linesArray[i][0],
            [key[1]]: linesArray[i][1],
            [key[2]]: linesArray[i][2],
            [key[3]]: linesArray[i][3],
            [key[4]]: linesArray[i][4]
          }
          self.$store.dispatch("register/pushCsvOutput",{info: info})
        }
      }
    }
  },
  watch: {
    show: function(){
      this.showContent = this.$store.state.register.showContent
    },
    watcher: function(){
      this.csvOutput = this.$store.state.register.csvOutput
    }
  }
}
</script>

<style scoped>
#overlay{
  z-index: 4;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
#content{
  z-index: 5;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
}
.modal-dialog{
  border-radius: 20px;
}
.modal-content{
  width: 1140px;
}
.close,.r-btn{
  font-size: 20px;
}
#result-table{
  overflow: scroll;
}
</style>