<template>
  <div>
    <div id="overlay" v-show="showContent" @click="closeDialog"></div>
    <div id="content" style="font-size: 20px;" v-show="showContent">
      <div class="modal-dialog modal-xl">
        <div class="modal-content">
          <div class="modal-header">
            <p>詳細情報</p>
            <button type="button" class="close" data-dismiss="modal"
            @click="closeDialog">&times;</button>
          </div>
          <div class="modal-body">
            <table class="table">
              <thead>
                <tr>
                  <th>コンテンツID</th>
                  <th>名称</th>
                  <th>種別</th>
                  <th>教科</th>
                  <th>対象学年</th>
                </tr>
              </thead>
              <tr>
                <td>{{moreInfo.id}}</td>
                <td>{{moreInfo.name}}</td>
                <td>{{moreInfo.kind}}</td>
                <td>{{moreInfo.subject}}</td>
                <td>{{moreInfo.grade}}</td>
              </tr>
            </table><br>
            <table class="table">
              <thead>
                <tr>
                  <th>概要</th>
                  <th>サムネイル</th>
                  <th>タイトル2</th>
                  <th>タイトル2(カナ)</th>
                  <th>制作日</th>
                </tr>
              </thead>
              <tr>
                <td>{{moreInfo.overview}}</td>
                <td>{{moreInfo.thumbnail}}</td>
                <td>{{moreInfo.title2}}</td>
                <td>{{moreInfo.title2kana}}</td>
                <td>{{moreInfo.productionDate}}</td>
              </tr>
            </table><br>
            <table class="table">
              <thead>
                <tr>
                  <th>準拠教科書番号</th>
                  <th>内容時間</th>
                  <th>有償/無償</th>
                  <th>認証要否</th>
                </tr>
              </thead>
              <tr>
                <td>{{moreInfo.applicableTextbookNumber}}</td>
                <td>{{moreInfo.contentTime}}</td>
                <td>{{moreInfo.cost}}</td>
                <td>{{moreInfo.certificationRequired}}</td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MoreInfo",
  data: function(){
    return {
      moreInfo: "",
      showContent: false
    }
  },
  computed: {
    show: function(){
      return this.$store.state.meta.showMore
    },
    setMore: function(){
      return this.$store.state.meta.watcher
    }
  },
  methods: {
    closeDialog: function(){
      this.$store.dispatch("meta/closeMore")
      this.$store.dispatch("dialog/dialogOpen")
    }
  },
  watch: {
    show: function(){
      this.showContent = this.$store.state.meta.showMore
    },
    setMore: function(){
      this.moreInfo = this.$store.state.meta.metaMore
    }
  }
}
</script>

<style scoped>
#overlay{
  z-index: 4;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
#content{
  z-index: 5;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
}
.modal-dialog{
  border-radius: 20px;
}
.modal-content{
  width: 1140px;
}
.close{
  font-size: 20px;
}
.btn{
  font-size: 20px;
}
</style>