export default {
  namespaced: true,
  state: {
    showContent: false,
    highLight :false
  },
  mutations: {
    open(state){
      state.showContent = true
    },
    close(state){
      state.showContent = false
    },
    on(state){
      state.highLight = true
    },
    off(state){
      state.highLight = false
    }
  },
  actions: {
    dialogOpen(ctx){
      ctx.commit("open")
    },
    dialogClose(ctx){
      ctx.commit("close")
    },
    onHighLight(ctx){
      ctx.commit("on")
    },
    offHighLight(ctx){
      ctx.commit("off")
    }
  },
}