<template>
  <div id="overlay" v-show="showContent" @click="closeDialog"></div>
  <div id="content" v-show="showContent">
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h2>コンテンツデータ参照</h2>
          <button type="button" class="close" data-dismiss="modal"
          @click="closeDialog">&times;</button>
        </div>
        <div class="modal-body">
          <div>
            <label>名称：</label>
            <input type="text" class="mr-3" v-model="name" style="font-size: 20px;">
            <label>種別：</label>
            <select class="mr-3" v-model="kind" style="font-size: 20px;">
              <option>種別を選択してください</option>
              <option v-for="k of kinds" v-bind:key="k.id">
                {{k.kind}}
              </option>
            </select><br>
            <label>教科：</label>
            <select class="mr-3" v-model="subject" style="font-size: 20px;">
              <option>教科を選択してください</option>
              <option v-for="s of subjects" v-bind:key="s.id">
                {{s.subject}}
              </option>
            </select>
            <label>対象学年：</label>
            <select class="mr-3" v-model="grade" style="font-size: 20px;">
              <option>対象学年を選択してください</option>
              <option v-for="g of grades" v-bind:key="g.id">
                {{g.grade}}
              </option>
            </select>
            <button class="btn btn-secondary" @click="search"
            style="font-size: 20px;">
              検索
            </button>
          </div><br>
          <div id="result-table" v-bind:class="{active: isHeight}">
            <table class="table">
              <thead>
                <tr>
                  <th>コンテンツID</th>
                  <th>名称</th>
                  <th>種別</th>
                  <th>教科</th>
                  <th>対象学年</th>
                  <th>詳細</th>
                  <th>選択</th>
                </tr>
              </thead>
              <tr v-for="r of result" v-bind:key="r.id">
                <td>{{r.id}}</td>
                <td>{{r.name}}</td>
                <td>{{r.kind}}</td>
                <td>{{r.subject}}</td>
                <td>{{r.grade}}</td>
                <td>
                  <button class="btn btn-secondary" @click="moreInfo">
                    詳細
                  </button>
                </td>
                <td>
                  <button class="btn btn-secondary" @click="selectMeta">
                    選択
                  </button>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoadMetaDlg",
  data: function(){
    return {
      showContent: this.$store.state.dialog.showContent,
      kinds: "",
      subjects: "",
      grades: "",
      name: "",
      kind: "種別を選択してください",
      subject: "教科を選択してください",
      grade: "対象学年を選択してください",
      result: [],
      isHeight: false
    }
  },
  computed: {
    show: function(){
      return this.$store.state.dialog.showContent
    },
    watcher: function(){
      return this.$store.state.meta.watcher
    }
  },
  methods: {
    closeDialog: function(){
      this.$store.dispatch("dialog/dialogClose")
    },
    search: function(){
      this.isHeight = true
      this.result.length = 0
      var kind = this.kind == "種別を選択してください" ? null : this.kind
      var subject = this.subject == "教科を選択してください" ? null : this.subject
      var grade = this.grade == "対象学年を選択してください" ? null : this.grade
      var sendParam = {
        name: this.name,
        kind: kind,
        subject: subject,
        grade: grade
      }
      this.axios.post("/api/meta",{param: sendParam})
        .then(res => {
          var data = res.data
          for(var i=0;i<data.length;i++){
            this.result.push({
              id: data[i][0],
              name: data[i][1],
              kind: data[i][3],
              subject: data[i][4],
              grade: data[i][5],
              overview: data[i][7],
              thumbnail: data[i][8],
              title2: data[i][9],
              title2kana: data[i][10],
              productionDate: data[i][11],
              applicableTextbookNumber: data[i][12],
              contentTime: data[i][13],
              cost: data[i][14],
              certificationRequired: data[i][15]
            })
          }
        })
    },
    selectMeta: function(e){
      this.closeDialog()
      var my = e.target
      var child = my.parentElement.parentElement.children
      this.$store.dispatch("meta/setId",{id: child[0].textContent})
      this.$store.dispatch("meta/setName",{name: child[1].textContent})
      this.$store.dispatch("meta/setKind",{kind: child[2].textContent})
      this.$store.dispatch("meta/setSubject",{subject: child[3].textContent})
      this.$store.dispatch("meta/setGrade",{grade: child[4].textContent})
    },
    moreInfo: function(e){
      var my = e.target
      var child = my.parentElement.parentElement.children
      var id = child[0].textContent
      for(var i=0;i<this.result.length;i++){
        if(id == this.result[i].id){
          this.$store.dispatch("meta/setMore",{more: this.result[i]})
        }
      }
      this.$store.dispatch("dialog/dialogClose")
      this.$store.dispatch("meta/openMore")
    }
  },
  beforeMount: function(){
    this.axios.get("/api/selectbox")
      .then(res => {
        var data = res.data
        var kind = data[0]
        var subject = data[1]
        var grade = data[2]
        var kinds = [],subjects = [],grades = []
        for(var i=0;i<kind.length;i++){
          kinds.push({
            id: i,
            kind: kind[i]
          })
        }
        for(i=0;i<subject.length;i++){
          subjects.push({
            id: i,
            subject: subject[i]
          })
        }
        for(i=0;i<grade.length;i++){
          grades.push({
            id: i,
            grade: grade[i]
          })
        }
        this.$store.dispatch("meta/setKinds",{kinds: kinds})
        this.$store.dispatch("meta/setSubjects",{subjects: subjects})
        this.$store.dispatch("meta/setGrades",{grades: grades})
      })
  },
  watch: {
    show: function(){
      this.showContent = this.$store.state.dialog.showContent
    },
    watcher: function(){
      this.kinds = this.$store.state.meta.metaKinds
      this.subjects = this.$store.state.meta.metaSubjects
      this.grades = this.$store.state.meta.metaGrades
    }
  }
}
</script>

<style scoped>
#overlay{
  z-index: 4;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
#content{
  z-index: 5;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
}
.modal-dialog{
  border-radius: 20px;
}
.modal-content{
  width: 1140px;
  font-size: 20px;
}
#result-table{
  overflow: scroll;
}
.close{
  font-size: 20px;
}
.active{
  height: 300px;
}
.btn{
  font-size: 15px;
}
</style>