export default {
  namespaced: true,
  state: {
    txt: "",
    pageNum: null,
    code: "",
    access: "",
    comment: "",
    showContent: false,
    csvOutput: [],
    watcher: 0,
    result: []
  },
  mutations: {
    setText(state,payload){
      state.txt = payload.txt
    },
    setPageNum(state,payload){
      state.pageNum = payload.num
    },
    setCode(state,payload){
      state.code = payload.code
    },
    setAccess(state,payload){
      state.access = payload.access
    },
    setComment(state,payload){
      state.comment = payload.comment
    },
    open(state){
      state.showContent = true
    },
    close(state){
      state.showContent = false
    },
    push(state,payload){
      state.csvOutput.push(payload.info)
      state.watcher++
    },
    delete(state,payload){
      state.csvOutput = payload.csv
      state.watcher++
    },
    result(state,payload){
      state.result = payload.result
    }
  },
  actions: {
    setText(ctx,payload){
      ctx.commit("setText",payload)
    },
    setPageNum(ctx,payload){
      ctx.commit("setPageNum",payload)
    },
    setCode(ctx,payload){
      ctx.commit("setCode",payload)
    },
    setAccess(ctx,payload){
      ctx.commit("setAccess",payload)
    },
    setComment(ctx,payload){
      ctx.commit("setComment",payload)
    },
    dialogOpen(ctx){
      ctx.commit("open")
    },
    dialogClose(ctx){
      ctx.commit("close")
    },
    pushCsvOutput(ctx,payload){
      ctx.commit("push",payload)
    },
    deleteCsvOutput(ctx,payload){
      ctx.commit("delete",payload)
    },
    setResult(ctx,payload){
      ctx.commit("result",payload)
    }
  }
}