var metaParams = () => ({
  metaId: "",
  metaName: "",
  metaKind: "",
  metaSubject: "",
  metaGrade: "",
  metaMore: "",
  metaKinds: "",
  metaGrades: "",
  metaSubjects: "",
  watcher: 0,
  moreInfo: "",
  showMore: false
})

export default {
  namespaced: true,
  state: metaParams,
  mutations: {
    setId(state,payload){
      state.metaId = payload.id
    },
    setName(state,payload){
      state.metaName = payload.name
    },
    setKind(state,payload){
      state.metaKind = payload.kind
    },
    setSubject(state,payload){
      state.metaSubject = payload.subject
    },
    setGrade(state,payload){
      state.metaGrade = payload.grade
    },
    setMore(state,payload){
      state.metaMore = payload.more
      state.watcher++
    },
    setKinds(state,payload){
      state.metaKinds = payload.kinds
      state.watcher++
    },
    setSubjects(state,payload){
      state.metaSubjects = payload.subjects
      state.watcher++
    },
    setGrades(state,payload){
      state.metaGrades = payload.grades
      state.watcher++
    },
    dialog(state,payload){
      state.showMore = payload
    }
  },
  actions: {
    setId(ctx,payload){
      ctx.commit("setId",payload)
    },
    setName(ctx,payload){
      ctx.commit("setName",payload)
    },
    setKind(ctx,payload){
      ctx.commit("setKind",payload)
    },
    setSubject(ctx,payload){
      ctx.commit("setSubject",payload)
    },
    setGrade(ctx,payload){
      ctx.commit("setGrade",payload)
    },
    setMore(ctx,payload){
      ctx.commit("setMore",payload)
    },
    setKinds(ctx,payload){
      ctx.commit("setKinds",payload)
    },
    setSubjects(ctx,payload){
      ctx.commit("setSubjects",payload)
    },
    setGrades(ctx,payload){
      ctx.commit("setGrades",payload)
    },
    setInfo(ctx,payload){
      ctx.commit("setInfo",payload)
    },
    openMore(ctx){
      ctx.commit("dialog",true)
    },
    closeMore(ctx){
      ctx.commit("dialog",false)
    }
  }
}